.workspaceHeaderWrapper {
  padding: 48px 100px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  .LogoWrapper {
    cursor: pointer;
  }
  .OptionWrapper {
    ul {
      list-style: none;
      li {
        float: left;
        margin-left: 65px;
        font-size: 2vw;
        color: #fff;
        font-weight: 500;
      }
      #selected {
        color: #e0c75b;
      }

      .myplanet {
        color: #1600ff;
      }
    }
  }
}
